.custom-list {
	list-style: none;
	margin: 0;
	padding: 30px 0;

	&__num {
		align-self: flex-start;
		margin-right: 12px;
		font-weight: 900;
		font-style: normal;
		color: $color-silver;
	}

	&__pic {
		display: flex;
		align-items: center;
		justify-content: center;
		margin-right: 0;
		margin-bottom: 20px;
		border-radius: 5px;
		padding: 15px;
		width: 90px;
		height: 90px;
		box-shadow:
			1px 1px 15px $color-alto,
			-1px -1px 15px $color-alto;
		background-color: $color-seashell;

		img {
			width: auto;
			height: auto;
			max-width: 100%;
			max-height: 100%;
		}

		@media (min-width: 590px) {
			margin-right: 40px;
			margin-bottom: 0;
		}
	}

	&__text {
		overflow: hidden;
		max-width: 400px;
	}

	&__item {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;

		&:not(:last-child) {
			margin-bottom: 54px;
		}

		@media (min-width: 590px) {
			flex-flow: row nowrap;
		}
	}

	&__strong {
		font-size: 15px;
		letter-spacing: 0.01em;
	}

	&__link {
		box-shadow:
			inset 0 -0.175em transparent,
			inset 0 -0.12em transparent;
		font-size: 15px;
		letter-spacing: 0.01em;
		text-decoration: none;
		text-shadow:
			-2px -2px transparent,
			-2px 2px transparent,
			2px -2px transparent,
			2px 2px transparent;
		color: $color-mine-shaft;
		transition: box-shadow 0.3s;

		@media (min-width: 1023px) {
			&:hover,
			&:focus {
				box-shadow:
					inset 0 -0.175em transparent,
					inset 0 -0.12em #000;
				transition: box-shadow 0.3s;
			}
		}
	}

	&__description {
		display: inline;
		margin: 0;
		font-size: 15px;
		letter-spacing: 0.02em;
	}
}
