*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	box-sizing: border-box;
	font-family: $font-open-sans;
	font-size: 16px;
}

body {
	width: 100%;
	height: 100%;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	width: 100%;
	max-width: 1240px;

	@media (min-width: 1279px) {
		padding: 0;
	}
}
