.services {
	position: relative;
	display: flex;
	flex-flow: column nowrap;
	padding-top: 20px;
	padding-bottom: 20px;
	min-height: calc(100vh - 70px);
	background-color: $color-seashell;

	&__wrapper {
		display: grid;
		justify-items: center;
		grid-template-columns: 1fr;

		@media (min-width: 1024px) {
			justify-items: flex-start;
			grid-template-columns: 1.2fr 0.8fr;
		}

		@media (min-width: 1279px) {
			grid-template-columns: 1fr 1fr;
		}
	}

	&__title {
		font-size: 34 / 400 * 100vw;
		letter-spacing: 0.005em;

		span {
			display: block;

			&:last-child {
				padding-top: 8px;
				font-weight: 400;
				font-size: 20 / 400 * 100vw;

				@media (min-width: 400px) {
					font-size: 22px;
				}
			}
		}

		@media (min-width: 400px) {
			font-size: 36px;
		}
	}

	&__picture {
		position: relative;
		overflow: hidden;
		margin-top: 27px;
		border-radius: 50%;
		width: 260 / 320 * 100vw;
		height: 260 / 320 * 100vw;
		box-shadow: 0 8px 40px $color-alto;

		img {
			position: absolute;
			left: 50%;
			top: 50%;
			min-width: 100%;
			min-height: 100%;
			transform: translate(-50%, -50%);
		}

		@media (min-width: 767px) {
			margin-left: 26px;
			width: 510 / 768 * 100vw;
			height: 510 / 768 * 100vw;
		}

		@media (min-width: 1024px) {
			width: 510 / 1280 * 100vw;
			height: 510 / 1280 * 100vw;
		}

		@media (min-width: 1279px) {
			width: 510px;
			height: 510px;
		}
	}

	@media (min-width: 767px) {
		padding-top: 40px;
		padding-bottom: 32px;
	}

	@media (min-width: 1023px) {
		padding-top: 70px;
		padding-bottom: 62px;
	}
}
