.main-header {
	position: relative;
	z-index: 5;
	padding-top: 12px;
	padding-bottom: 11px;
	box-shadow: 5px 0 10px $color-alto;
	background-color: $color-white;

	&__wrapper {
		display: grid;
		align-content: center;
		justify-items: center;
		grid-template-columns: 1fr;

		@media (min-width: 480px) {
			grid-template-columns: 1fr 2fr;
		}

		@media (min-width: 768px) {
			grid-template-columns: 1fr;
		}

		@media (min-width: 900px) {
			justify-content: space-between;
			grid-template-columns: 184px 1fr;
		}
	}

	&__logo {
		display: inline-block;
		margin-bottom: 10px;
		width: 184px;
		text-decoration: none;
		transition: opacity 0.3s;

		img {
			margin-bottom: 3px;
			height: auto;
			max-width: 100%;
		}

		figcaption {
			font-size: 12px;
			line-height: 1;
			letter-spacing: 0.065em;
			text-transform: uppercase;
			color: $color-tundora;
		}

		@media (min-width: 900px) {
			margin-bottom: 0;
		}

		@media (min-width: 1023px) {
			&:hover,
			&:focus {
				outline: none;
				opacity: 0.7;
				transition: opacity 0.3s;
			}
		}
	}

	&__nav {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;

		@media (min-width: 480px) {
			align-items: flex-end;
			justify-self: flex-end;
		}

		@media (min-width: 767px) {
			flex-flow: row nowrap;
			align-items: center;
			justify-self: center;
		}

		@media (min-width: 900px) {
			justify-self: flex-end;
		}
	}

	&__callback {
		position: relative;
		border: none;
		border-radius: 20px;
		padding: 9px 20px 8px 56px;
		font-size: 14px;
		letter-spacing: 0.05em;
		text-transform: uppercase;
		color: $color-tundora;
		background-color: $color-gallery;
		transition: color 0.3s;

		.phone-ic {
			position: absolute;
			left: 2px;
			top: 2px;
			display: inline-block;
			border-radius: 50%;
			width: 32px;
			height: 32px;
			background-image: url("../images/sprites/png/tel.png");
			background-position: 55% 52%;
			background-size: 42%;
			background-repeat: no-repeat;
			background-color: $color-white;
			transform: rotate(0);
			transition: transform 0.5s;
		}

		@media (min-width: 1023px) {
			&:hover,
			&:focus {
				color: $color-black;
				transition: color 0.3s;

				.phone-ic {
					transform: rotate(720deg);
					transition: transform 0.5s;
				}
			}
		}
	}

	&__links {
		display: flex;
		align-items: center;
		padding-top: 10px;
		padding-bottom: 10px;

		@media (min-width: 767px) {
			padding: 2px 15px 0;
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		margin: 0 10 / 400 * 100vw;
		padding: 10px 0;
		font-size: 14px;
		line-height: 1;
		letter-spacing: 0.07em;
		text-decoration: none;
		text-transform: uppercase;
		color: $color-mine-shaft;

		&::after {
			content: "";
			position: absolute;
			left: 50%;
			bottom: 5px;
			width: 0;
			height: 1px;
			background-color: $color-mine-shaft;
			transition: width 0.3s, left 0.3s;
		}

		@media (min-width: 400px) {
			margin: 0 15px;
		}

		@media (min-width: 480px) {
			margin-right: 0;
		}

		@media (min-width: 767px) {
			margin-right: 15px;
		}

		@media (min-width: 1023px) {
			&:hover,
			&:focus {
				&::after {
					left: 0;
					width: 100%;
					transition: width 0.3s, left 0.3s;
				}
			}
		}
	}

	&__form {
		font-size: 0;
	}

	&__sign {
		display: inline-block;
		vertical-align: middle;
		border: 2px solid $color-mine-shaft;
		border-radius: 20px;
		padding: 8px 21px;
		font-weight: 700;
		font-size: 14px;
		line-height: 1;
		text-align: center;
		text-decoration: none;
		text-transform: uppercase;
		color: $color-white;
		background-color: $color-mine-shaft;
		transition: color 0.3s, background-color 0.3s;

		@media (min-width: 1023px) {
			&:hover,
			&:focus {
				color: $color-mine-shaft;
				background-color: transparent;
				transition: color 0.3s, background-color 0.3s;
			}
		}
	}

	&__basket {
		display: inline-block;
		vertical-align: middle;
		margin-left: 27px;
		padding-right: 2px;

		svg {
			width: 24px;
			height: 30px;
			fill: $color-mine-shaft;
			transform: scale(1);
			transition: fill 0.3s, transform 0.3s;
		}

		@media (min-width: 1023px) {
			&:hover,
			&:focus {
				svg {
					fill: $color-silver;
					transform: scale(1.1);
					transition: fill 0.3s, transform 0.3s;
				}
			}
		}
	}
}
